<template>
  <div class="flex">
    <div class="flex-grow">
      <div class="flex items-center flex-wrap">
        <p class="font-semibold mr-4 mb-1">{{ title || '' }}</p>
        <span v-if="total" class="p-2 w-2 h-2 text-xs flex items-center justify-center box-content bg-teal-04 text-white rounded-full">
          {{ total >= 99 ? '99+' : total }}
        </span>
      </div>
      <div class="flex items-center h-4" v-if="date">
        <p class="text-sm mr-2 text-teal-04">{{ date || '' }}</p>
      </div>
    </div>
    <div class="flex-grow flex justify-end">
      <div class="block">
        <div class="bg-teal-lighten text-teal-04 text-xs px-2 py-1 box-border rounded flex items-center cursor-pointer">
          <i class="material-icons text-sm align-text-middle">visibility</i>
          <span class="ml-2">View All</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, toRef } from 'vue'

export default defineComponent({
  props: {
    title: {
      type: String,
      default: ''
    },
    date: {
      type: String,
      default: ''
    },
    total: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const title = toRef(props, 'title')
    const date = toRef(props, 'date')
    const total = toRef(props, 'total')
    return {
      title,
      date,
      total
    }
  },
})
</script>
